*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.kilopiu
{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    max-width: 100%;
    background-color: #000000;
}
#time2
{
    display: flex;
    gap:  40px;
    color: #fff;
} 
#time2 .circle
{
    position: relative;
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}
#time2 .circle svg
{
    position: relative;
    width: 150px;
    height: 150px;
    transform: rotate(270deg);
}
#time2 .circle svg circle
{
    width: 100%;
    height: 100%;
    fill: transparent;

    stroke-width: 4;
    transform: translate(5px,5px);
}
#time2 .circle svg circle:nth-child(2)
{
    stroke: var(--clr);  
    stroke-dasharray: 440;
}
#time2 div
{
    position: absolute;
    text-align: center;
    font-weight: 500;
    font-size: 1.5em;
}
#time2 .ap
{
    position: relative;
    font-size: 1em;
    transform: translateX(-20px);
}
.dots
{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    display: flex;
    justify-content: center;
}
.dots::before
{
    content: '';
    position: absolute;
    top: -3px;
    width: 15px;
    height: 15px;
    background: var(--clr);
    border-radius: 50%;
    box-shadow: 0 0 20px var(--clr),
    0 0 60px var(--clr);
}
#time2 div span
{
    position: absolute;
    transform:  translateX(-50%)  translateY(-0px);
    font-size: 0.35em;
    font-weight: 300;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}
.red{
    color: red;
    font-size: 20px;
     font-family: 'Courier New', Courier, monospace;
     display: none;
}
@media (max-width:628px) {
    #time2{
        display: none;
    }
    .red{
        color: red;
        font-size: 20px;
         font-family: 'Courier New', Courier, monospace;
         display: block;
         text-align: center;
        }
}